import React from 'react';
import { BrowserRouter as Router, Route,Switch} from 'react-router-dom';
import './Style/Contents.css'
import Home from './Pages/Home';
import Blog from './Pages/Blog';
import Gallery from './Pages/Gallery'
function Contents(props){
    let currentPage = null;
    switch(props.url){
        case process.env.PUBLIC_URL + '/':
            currentPage = <Home setTitle={props.setTitle} setUrl={props.setUrl}/>;
            break;
        case process.env.PUBLIC_URL + '/home':
            currentPage = <Home setTitle={props.setTitle} setUrl={props.setUrl}/>;
            break;
        case process.env.PUBLIC_URL + '/blog':
            currentPage = <Blog setTitle={props.setTitle} setblogcontent={props.setblogcontent} setUrl={props.setUrl}/>;
            break;
        case process.env.PUBLIC_URL + '/gallery':
            currentPage = <Gallery setTitle={props.setTitle} setUrl={props.setUrl}/>;
            break;
    }
    if(props.url == ''){
        currentPage =(
        <Router>
            <Switch>
            <Route exact path={process.env.PUBLIC_URL+'/'} render={() => <Home setTitle={props.setTitle} setUrl={props.setUrl}/> }/>
            <Route exact path={process.env.PUBLIC_URL+'/home'} render={() => <Home setTitle={props.setTitle} setUrl={props.setUrl}/>}/>
            <Route exact path={process.env.PUBLIC_URL+'/blog'} render={() => <Blog setTitle={props.setTitle} setblogcontent={props.setblogcontent} setUrl={props.setUrl}/>}/>
            <Route exact path={process.env.PUBLIC_URL+'/gallery'} render={() => <Gallery setTitle={props.setTitle} setUrl={props.setUrl}/>}/>
            </Switch>
        </Router>
        )
    }
    return (
        <div>
            {currentPage}

           
        </div>
    )
}
export default Contents;